<template>
  <div className="admin-manage-page">
    <el-row >
      <el-col :span=8>
        <div class="grid-content bg-purple">
          <div className="basic-form">
            <basic-container>
              <el-form :model="searchForm"  ref="searchForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="搜索ID" prop="adminId">
                  <el-input v-model="searchForm.adminId"></el-input>
                </el-form-item>
                <el-form-item label="搜索账号" prop="adminAccount">
                  <el-input v-model="searchForm.adminAccount"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitSearchForm()">搜索</el-button>
                  <el-button @click="resetSearchForm('searchForm')">重置</el-button>
                </el-form-item>
              </el-form>
            </basic-container>
          </div>
        </div>
        <div class="grid-content bg-purple">
          <div className="basic-form">
            <basic-container>
              <el-form :model="newForm"  ref="newForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="新增管理员" prop="adminAccount">
                  <el-input v-model="newForm.adminAccount"></el-input>
                </el-form-item>
                <el-form-item label="输入密码" prop="adminPassword">
                  <el-input type="password" v-model="newForm.adminPassword"></el-input>
                </el-form-item>
                <el-form-item label="确认密码密码" prop="adminPassword_confirm">
                  <el-input type="password" v-model="newForm.adminPassword_confirm"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitNewForm()">创建</el-button>
                  <el-button @click="resetNewForm('newForm')">重置</el-button>
                </el-form-item>
              </el-form>
              
            </basic-container>
          </div>
        </div>
      </el-col>
      <el-col :span=1><span>&nbsp;</span></el-col>
      <el-col :span=15>
        <div class="grid-content bg-purple">
          <div className="fixed-table">
            <basic-container>
              <el-table :data="tableData" border style="width: 100%" height="800px" >
                <el-table-column sortable prop="admin_id" label="ID" width="160">
                </el-table-column>
                <el-table-column sortable prop="admin_account" label="账号" width="180">
                </el-table-column>
                <el-table-column label="操作" width="200">
                  <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">修改密码</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除账户</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div style="text-align: center;">
              <el-pagination background  layout="prev, pager, next" @current-change="handleCurrentChange" :total="50" :current-page.sync="currentpage">
              </el-pagination>
            </div>
            </basic-container>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// import FixedTable from './components/FixedTable';
import BasicContainer from "@vue-materials/basic-container";
import axios from "../../api/http.js";
const searchParams = {
  adminId: "",
  adminAccount: "",
  url: "",
  pageNum: 1
};

export default {
  name: "AdminManage",
  components: {
    BasicContainer
  },

  //   components: { BasicContainer },
  // name: 'FixedTable',
  data() {
    return {
      tableData: [
        // {
        //   admin_id: 1,
        //   admin_account: "admin"
        // }
      ],
      searchForm: {
        adminId: "",
        adminAccount: ""
      },
      newForm: {
        adminAccount: "",
        adminPassword: "",
        adminPassword_confirm: ""
      },
      currentpage: 1
    };
  },
  mounted: function() {
    (searchParams.adminId = null), (searchParams.adminAccount = null);
    (searchParams.url = "/manage/administrators"), (searchParams.pageNum = 1);
    this.getTable(searchParams.url, searchParams);
  },
  methods: {
    handleEdit(index, row) {
      console.log(index, row);
      let account = row.admin_account;
      let oldPassword = "";
      let newPassword = "";
      this.$prompt("请输入原密码", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(({ value }) => {
          oldPassword = value;
          this.$prompt("请输入新密码", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消"
          })
            .then(({ value }) => {
              newPassword = value;
              let resetPasswordParams = new URLSearchParams();
              resetPasswordParams.append("oldPassword", oldPassword);
              resetPasswordParams.append("newPassword", newPassword);
              if (account != "" && oldPassword != "" && newPassword != "") {
                axios({
                  method: "POST",
                  url: "/manage/administrators/account/" + account + "/password",
                  params: resetPasswordParams
                }).then(res => {
                  if (res.data.code == 1) {
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      type: "warning",
                      message: res.data.msg
                    });
                  }
                });
              }
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "取消修改"
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消修改"
          });
        });
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
            center: true
          })
          .then(() => {
                    axios({
          method: "DELETE",
          url: "/manage/administrators/" + row.admin_id
        }).then(res => {
          if (res.data.code == 1) {
            this.$message({
              type: "success",
              message: res.data.msg
            });
          } else {
            this.$message({
              type: "warning",
              message: res.data.msg
            });
          }
        });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
    },
    submitSearchForm() {
      if (this.searchForm.adminId != "") {
        (searchParams.adminId = this.searchForm.adminId),
          (searchParams.adminAccount = null);
        (searchParams.url =
          "/manage/administrators/" + this.searchForm.adminId),
          (searchParams.pageNum = 1);
      } else if (this.searchForm.adminAccount != null) {
        (searchParams.adminId = null),
          (searchParams.adminAccount = this.searchForm.adminAccount);
        (searchParams.url =
          "/manage/administrators/account/" + this.searchForm.adminAccount),
          (searchParams.pageNum = 1);
      }
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },
    submitNewForm() {
      if (this.newForm.adminAccount == "") {
        this.$message({
          type: "warning",
          message: "请管理员Account"
        });
      }
      if (
        this.newForm.adminPassword != this.newForm.adminPassword_confirm ||
        this.newForm.adminPassword.length < 8
      ) {
        this.$message({
          type: "warning",
          message: "请确认两次密码一致且长度大于8."
        });
      }

      let newAccountParams = new URLSearchParams();
      newAccountParams.append("adminAccount", this.newForm.adminAccount);
      newAccountParams.append("adminPassword", this.newForm.adminPassword);
      axios({
        method: "put",
        url: "/manage/administrators",
        params: newAccountParams
      }).then(res => {
        if (res.data.code == 1) {
          this.$message({
            type: "success",
            message: res.data.msg
          });
          this.resetNewForm("newForm");
        } else if (res.data.code < 1) {
          this.$message({
            type: "warning",
            message: res.data.msg
          });
        }
      });
    },
    resetSearchForm(formName) {
      this.$refs[formName].resetFields();
    },
    resetNewForm(formName) {
      this.$refs[formName].resetFields();
    },
    getTable(targeturl, getparams) {
      console.log("url : " + targeturl + "   params : " + getparams);
      axios({
        method: "GET",
        url: targeturl,
        params: getparams
      }).then(res => {
        this.tableData = res.data.data;
        if (res.data.code < 1) {
          this.$message({
            type: "warning",
            message: res.data.msg
          });
        }
      });
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      searchParams.pageNum = val;
      this.getTable(searchParams.url, searchParams);
    }
  }
};
</script>