<template>
  <div className="user-manage-page">
    <el-row>
      <el-col :span=8>
        <div class="grid-content bg-purple">
          <div class="search">
            <div className="basic-form">
              <basic-container >
                <el-form :model="userSearchForm" style="width:100%" ref="userSearchForm" label-width="100px" class="demo-ruleForm" >
                  <el-form-item label="用户ID" prop="userId">
                    <el-input v-model="userSearchForm.userId"></el-input>
                  </el-form-item>
                  <el-form-item label="用户号码" prop="userPhone">
                    <el-input v-model="userSearchForm.userPhone"></el-input>
                  </el-form-item>
                  <el-form-item label="用户昵称" prop="userAlias">
                    <el-input v-model="userSearchForm.userAlias"></el-input>
                  </el-form-item>
                  <el-form-item>
                    <el-button type="primary" @click="submitForm('userSearchForm')">搜索</el-button>
                    <el-button @click="resetForm('userSearchForm')">重置</el-button>
                  </el-form-item>
                </el-form>
              </basic-container>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :span=1><span>&nbsp;</span></el-col>
      <el-col :span=15>
        <div class="grid-content bg-purple-light">
          <div class="table">
            <basic-container>
            <el-table :data="tableData" border style="width: 100%" height="800px">
              <el-table-column sortable fixed prop="user_id" label="ID" width="150">
              </el-table-column>
              <el-table-column sortable prop="user_alias" label="用户昵称" width="180">
              </el-table-column>
              <el-table-column sortable prop="user_phone" label="用户手机" width="200">
              </el-table-column>
              <el-table-column prop="user_balance" label="用户余额" width="160">
              </el-table-column>
              <el-table-column label="操作" width="280">
                <template slot-scope="scope">
                  <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button size="mini" type="success" @click="handleRecharge(scope.$index, scope.row)">充值</el-button>
                  <el-button size="mini" type="warning" @click="handleDudect(scope.$index, scope.row)">扣费</el-button>
                  <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div style="text-align: center;">
              <el-pagination background layout="prev, pager, next" @current-change="handleCurrentChange" :total="50" :current-page="currentpage">
              </el-pagination>
            </div>
            </basic-container>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  // import BasicForm from "./components/BasicForm";
  // import FixedTable from "./components/FixedTable";
  import BasicContainer from "@vue-materials/basic-container";
  import axios from "../../api/http.js";

  const searchParams = {
    userId: "",
    userPhone: "",
    userAlias: "",
    pageNum: 1
  };

  export default {
    name: "UserManage",
    data() {
      return {
        tableData: [],
        userSearchForm: {
          userId: "",
          userPhone: "",
          userAlias: ""
        },
        currentpage:1
      };
    },
    components: {
      BasicContainer
    },
    mounted: function () {
      (searchParams.pageNum = 1), this.getList();
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
        this.$prompt("请输入改绑手机号", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            inputPattern: /^[1][3,4,5,7,8][0-9]{9}$/,
            inputErrorMessage: "请输入正确手机号码"
          })
          .then(({
            value
          }) => {
            this.changePhone(row.user_id, value);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "取消改绑"
            });
          });
      },
      handleDelete(index, row) {
        this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
            center: true
          })
          .then(() => {
            this.deleteUser(row.user_id);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      },
      handleRecharge(index, row) {
        this.$prompt("请输入充值金额", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            inputPattern: /^\d+(\.{0,1}\d+){0,1}$/,
            inputErrorMessage: "充值金额不正确"
          })
          .then(({
            value
          }) => {
            this.recharge(row.user_id, value);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "取消充值"
            });
          });
      },
      handleDudect(index, row) {
        this.$prompt("请输入扣除金额", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            inputPattern: /^\d+(\.{0,1}\d+){0,1}$/,
            inputErrorMessage: "扣费金额不正确"
          })
          .then(({
            value
          }) => {
            this.recharge(row.user_id, -value);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "取消扣费"
            });
          });
      },
      submitForm() {
        (searchParams.userId = this.userSearchForm.userId),
        (searchParams.userPhone = this.userSearchForm.userPhone),
        (searchParams.userAlias = this.userSearchForm.userAlias),
        (searchParams.pageNum = 1);

        if (searchParams.userId != "") {
          this.getOne();
        } else {
          this.getList();
        }
      },
      getList() {
        axios({
          method: "GET",
          url: "/manage/users",
          params: searchParams
        }).then(res => {
          this.tableData = res.data.data;
        });
      },

      getOne() {
        axios({
          method: "GET",
          url: "/manage/users/" + searchParams.userId,
          params: searchParams
        }).then(res => {
          this.tableData = res.data.data;
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
        (searchParams.pageNum = val), this.getList();
      },
      recharge(userId, money) {
        let rechargeParams = new URLSearchParams();
        rechargeParams.append("money", money);
        axios({
          method: "POST",
          url: "/manage/users/" + userId + "/balance",
          params: rechargeParams
        }).then(res => {
          if (res.data.code == 1) {
            this.$message({
              type: "success",
              message: res.data.msg + "请刷新。"
            });
          } else {
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      },

      changePhone(userId, phone) {
        let rechargeParams = new URLSearchParams();
        rechargeParams.append("phone", phone);
        axios({
          method: "POST",
          url: "/manage/users/" + userId + "/phone",
          params: rechargeParams
        }).then(res => {
          if (res.data.code == 1) {
            this.$message({
              type: "success",
              message: res.data.msg + "请刷新。"
            });
          } else {
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      },
      deleteUser(userId) {
        axios({
          method: "DELETE",
          url: "/manage/users/" + userId
        }).then(res => {
          if (res.data.code == 1) {
            this.$message({
              type: "success",
              message: res.data.msg + "请刷新。"
            });
          } else {
            this.$message({
              type: "success",
              message: res.data.msg
            });
          }
        });
      }
    }
  };
</script>