<template>
  <div className="power-bank-manage-page">
        <el-row >
      <el-col :span=8>
        <div class="grid-content bg-purple">
          <div className="basic-form">
            <basic-container>
              <el-form :model="searchForm"  ref="searchForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="搜索ID" prop="pobk_id">
                  <el-input v-model="searchForm.pobk_id"></el-input>
                </el-form-item>
                 <el-form-item>
                  <el-button type="primary" @click="submitSearchFormById()">搜索</el-button>
                </el-form-item>
                <el-form-item label="投放点ID搜索" prop="pobk_location_id">
                  <el-input v-model="searchForm.pobk_location_id"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitSearchFormByLocationId()">搜索</el-button>
                </el-form-item>
              </el-form>
            </basic-container>
          </div>
        </div>
        <div class="grid-content bg-purple">
          <div className="basic-form">
            <basic-container>
              <el-form :model="newForm"  ref="newForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="投放点ID" prop="pobk_location_id">
                  <el-input v-model="newForm.pobk_location_id"></el-input>
                </el-form-item>
                <el-form-item label="投放数量" prop="pobk_amount">
                  <el-input v-model="newForm.pobk_amount"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitNewForm()">投放</el-button>
                  <el-button @click="resetNewForm('newForm')">重置</el-button>
                </el-form-item>
              </el-form>
              
            </basic-container>
          </div>
        </div>
      </el-col>
      <el-col :span=1><span>&nbsp;</span></el-col>
      <el-col :span=15>
        <div class="grid-content bg-purple">
          <div className="fixed-table">
            <basic-container>
              <el-table :data="tableData" border style="width: 100%" height="800px" >
                <el-table-column sortable prop="pobk_id" label="ID" width="160">
                </el-table-column>
                <el-table-column sortable prop="pobk_location_id" label="地点ID" width="180">
                </el-table-column>
                <el-table-column sortable prop="pobk_status" label="状态" width="180">
                </el-table-column>
                <el-table-column label="操作" width="220">
                  <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">转移投放点</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除充电宝</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div style="text-align: center;">
              <el-pagination background  layout="prev, pager, next" @current-change="handleCurrentChange" :total="50" :current-page.sync="currentpage">
              </el-pagination>
            </div>
            </basic-container>
          </div>
        </div>
      </el-col>
    </el-row>
      </div>
</template>

<script>
import BasicContainer from "@vue-materials/basic-container";
import axios from "../../api/http.js";
const searchParams = {
  pobk_id:null,
  pobk_location_id:null,
  url: "",
  pageNum: 1
};
export default {
  name: 'PowerBankManage',
  components: {
    BasicContainer
  },
  
  data() {
    return {
      tableData: [
        // {
        //   admin_id: 1,
        //   admin_account: "admin"
        // }
        {
          pobk_id:1,
          pobk_location_id:10,
          pobk_status:"available"
        }
      ],
      searchForm: {
        pobk_id: "",
        pobk_location_id: ""
      },
      newForm: {
        pobk_location_id: "",
        pobk_amount:null,
      },
      currentpage: 1
    };
  },
  mounted: function() {

        (searchParams.pobk_id = null),
          (searchParams.pobk_location_id = null);
        (searchParams.url = "/manage/powerbanks/"),
          (searchParams.pageNum = 1);
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
  },
  methods: {
    handleEdit(index, row) {
console.log(index, row);
      let pobk_id = row.pobk_id;
      let pobk_location_id = null;
       this.$prompt("请输入新投放点ID", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消"
          })
            .then(({ value }) => {
              pobk_location_id = value;
              let updateParams = new URLSearchParams();
              updateParams.append("pobk_id", pobk_id)
              updateParams.append("pobk_location_id",pobk_location_id)
              if (pobk_id != "" && pobk_location_id != "" ) {
                axios({
                  method: "POST",
                  url:
                    "/manage/powerbanks/" + pobk_id,
                  params: updateParams
                }).then(res => {
                  if (res.data.code == 1) {
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      type: "warning",
                      message: res.data.msg
                    });
                  }
                });
              }
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "取消修改"
              });
            });
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.$confirm("此操作将永久删除该充电宝, 是否继续?", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
            center: true
          })
          .then(() => {
                    axios({
          method: "DELETE",
          url: "/manage/powerbanks/" + row.pobk_id
        }).then(res => {
          if (res.data.code == 1) {
            this.$message({
              type: "success",
              message: res.data.msg
            });
          } else {
            this.$message({
              type: "warning",
              message: res.data.msg
            });
          }
        });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
    },
    submitSearchFormById() {
      if (this.searchForm.pobk_id != "") {
        (searchParams.pobk_id = this.searchForm.pobk_id),
          (searchParams.pobk_location_id = null);
        (searchParams.url =
          "/manage/powerbanks/" + searchParams.pobk_id),
          (searchParams.pageNum = 1);
      } 
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },
        submitSearchFormByLocationId() {
      if (this.searchForm.pobk_location_id != "") {
        (searchParams.pobk_id = null),
          (searchParams.pobk_location_id = this.searchForm.pobk_location_id);
        (searchParams.url = "/manage/powerbanks/" ),
          (searchParams.pageNum = 1);
      } 
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },
    submitNewForm() {
      if (this.newForm.pobk_location_id != "" && this.newForm.pobk_amount != "") {

      }

      let newPobkParams = new URLSearchParams();
      newPobkParams.append("pobk_location_id", this.newForm.pobk_location_id);
      newPobkParams.append("pobk_amount", this.newForm.pobk_amount);
      axios({
        method: "put",
        url: "/manage/powerbanks",
        params: newPobkParams
      }).then(res => {
        if (res.data.code == 1) {
          this.$message({
            type: "success",
            message: res.data.msg
          });
          this.resetNewForm("newForm");
        } else if (res.data.code < 1) {
          this.$message({
            type: "warning",
            message: res.data.msg
          });
        }
      });
    },
    resetSearchForm(formName) {
      this.$refs[formName].resetFields();
    },
    resetNewForm(formName) {
      this.$refs[formName].resetFields();
    },
    getTable(targeturl, getparams) {
      console.log("url : " + targeturl + "   params : " + getparams);
      axios({
        method: "GET",
        url: targeturl,
        params: getparams
      }).then(res => {
        this.tableData = res.data.data;
        if (res.data.code < 1) {
          this.$message({
            type: "warning",
            message: res.data.msg
          });
        }
      });
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      searchParams.pageNum = val;
      this.getTable(searchParams.url, searchParams);
    }
  }
};


</script>
