<template>
  <div className="order-manage-page">

        <div class="grid-content bg-purple">
          <div className="basic-form">
            <basic-container>
              <el-form :model="searchForm"  ref="searchForm" label-width="100px" class="demo-ruleForm" :inline=true>
                <el-form-item label="搜索ID" prop="order_id">
                  <el-input v-model="searchForm.order_id"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitSearchFormById()">搜索</el-button>
                </el-form-item>
                <el-form-item label="用户ID" prop="order_user_id">
                  <el-input v-model="searchForm.order_user_id"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitSearchFormByUserId()">搜索</el-button>
                </el-form-item>
  
              </el-form>
            </basic-container>
          </div>
        </div>
        
        <div class="grid-content bg-purple">
          <div className="fixed-table">
            <basic-container>
              <el-table :data="tableData" border style="width: 100%" height="800px" >
                <el-table-column sortable prop="order_id" label="ID" width="80">
                </el-table-column>
                <el-table-column sortable prop="order_user_id" label="用户ID" width="100">
                </el-table-column>
                <el-table-column sortable prop="order_lent_location_id" label="租借地点ID" width="115">
                </el-table-column>
                <el-table-column sortable prop="order_pobk_id" label="充电宝ID" width="105">
                </el-table-column>
                <el-table-column sortable prop="order_create_time" label="创建时间" width="210">
                </el-table-column>
                <el-table-column sortable prop="order_has_finished" label="是否完成" width="105">
                </el-table-column>
                <el-table-column sortable prop="order_revert_location_id" label="归还地点ID" width="115">
                </el-table-column>
                <el-table-column sortable prop="order_finish_time" label="结束时间" width="210">
                </el-table-column>
                <el-table-column sortable prop="order_cost" label="产生费用" width="105">
                </el-table-column>
                <el-table-column label="操作" width="160">
                  <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEditAddress(scope.$index, scope.row)">完成</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div style="text-align: center;">
              <el-pagination background  layout="prev, pager, next" @current-change="handleCurrentChange" :total="50" :current-page.sync="currentpage">
              </el-pagination>
            </div>
            </basic-container>
          </div>
        </div>

      </div>
</template>

<script>
import BasicContainer from "@vue-materials/basic-container";
import axios from "../../api/http.js";
const searchParams = {
  order_id: null,
  order_user_id: null,
  url: "",
  pageNum: 1
};
export default {
  name: "OrderManage",
  components: {
    BasicContainer
  },
  data() {
    return {
      tableData: [
        {
          order_id: 1,
          order_user_id: 2,
          order_lent_location_id: 10,
          order_pobk_id: 2,
          order_create_time: "2018-07-02 20:13:00",
          order_has_finished: "false",
          order_revert_location_id: 11,
          order_finish_time: "2018-07-02 20:55:00",
          order_cost: 0.5
        }
      ],
      searchForm: {
        order_id: null,
        order_user_id: null
      },
      currentpage: 1
    };
  },
  mounted: function() {
    searchParams.order_id = null;
    searchParams.order_user_id = null;
    (searchParams.url = "/manage/orders"), (searchParams.pageNum = 1);
    this.currentpage = 1;
    this.getTable(searchParams.url, searchParams);
  },
  methods: {
    handleEditAddress(index, row) {
      console.log(index, row);
      let locationId = row.location_id;
      let newAddress = null;
      this.$prompt("请输入新地址", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      })
        .then(({ value }) => {
          newAddress = value;
          let updateParams = new URLSearchParams();

          updateParams.append("address", newAddress);
          if (locationId != "" && newAddress != "") {
            axios({
              method: "POST",
              url: "/manage/locations/" + locationId,
              params: updateParams
            }).then(res => {
              if (res.data.code == 1) {
                this.$message({
                  type: "success",
                  message: res.data.msg
                });
              } else {
                this.$message({
                  type: "warning",
                  message: res.data.msg
                });
              }
            });
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "取消修改"
          });
        });
    },

    handleDelete(index, row) {
      console.log(index, row);
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          axios({
            method: "DELETE",
            url: "/manage/administrators/" + row.admin_id
          }).then(res => {
            if (res.data.code == 1) {
              this.$message({
                type: "success",
                message: res.data.msg
              });
            } else {
              this.$message({
                type: "warning",
                message: res.data.msg
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    submitSearchFormById() {
      searchParams.order_id = this.searchForm.order_id;
      searchParams.order_user_id = null;
      (searchParams.url = "/manage/orders/" + searchParams.order_id),
        (searchParams.pageNum = 1);
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },

    submitSearchFormByUserId() {
      searchParams.order_id = null;
      searchParams.order_user_id = this.searchForm.order_user_id;
      (searchParams.url = "/manage/orders"), (searchParams.pageNum = 1);
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },

    resetSearchForm(formName) {
      this.$refs[formName].resetFields();
    },

    getTable(targeturl, getparams) {
      console.log("url : " + targeturl + "   params : " + getparams);
      axios({
        method: "GET",
        url: targeturl,
        params: getparams
      }).then(res => {
        this.tableData = res.data.data;
        if (res.data.code < 1) {
          this.$message({
            type: "warning",
            message: res.data.msg
          });
        }
      });
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      searchParams.pageNum = val;
      this.getTable(searchParams.url, searchParams);
    }
  }
};
</script>
