<template>
  <div className="location-manage-page">
       <el-row >
      <el-col :span=8>
        <div class="grid-content bg-purple">
          <div className="basic-form">
            <basic-container>
              <el-form :model="searchForm"  ref="searchForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="搜索ID" prop="location_id">
                  <el-input v-model="searchForm.location_id"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitSearchFormById()">搜索</el-button>
                </el-form-item>
                <el-form-item label="城市" prop="location_city">
                  <el-input v-model="searchForm.location_city"></el-input>
                </el-form-item>
                <el-form-item label="地区" prop="location_district">
                  <el-input v-model="searchForm.location_district"></el-input>
                </el-form-item>
                <el-form-item label="地址" prop="location_address">
                  <el-input v-model="searchForm.location_address"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitSearchFormByAddress()">搜索</el-button>
                  <el-button @click="resetSearchForm('searchForm')">重置</el-button>
                </el-form-item>
                <el-form-item label="名称" prop="location_alias">
                  <el-input v-model="searchForm.location_alias"></el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="submitSearchFormByAlias()">搜索</el-button>
                </el-form-item>
              </el-form>
            </basic-container>
          </div>
        </div>
        
      </el-col>
      <el-col :span=1><span>&nbsp;</span></el-col>
      <el-col :span=15>
        <div class="grid-content bg-purple">
          <div className="fixed-table">
            <basic-container>
              <el-table :data="tableData" border style="width: 100%" height="800px" >
                <el-table-column sortable prop="location_id" label="ID" width="80">
                </el-table-column>
                <el-table-column sortable prop="location_city" label="城市" width="80">
                </el-table-column>
                <el-table-column sortable prop="location_district" label="地区" width="80">
                </el-table-column>
                <el-table-column sortable prop="location_address" label="地址" width="230">
                </el-table-column>
                <el-table-column sortable prop="location_alias" label="名称" width="120">
                </el-table-column>
                <el-table-column sortable prop="location_amount" label="投放容量" width="60">
                </el-table-column>
                <el-table-column sortable prop="location_available" label="可用数量" width="60">
                </el-table-column>
                <el-table-column label="操作" width="280">
                  <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEditAddress(scope.$index, scope.row)">地址</el-button>
                    <el-button size="mini" type="primary" @click="handleEditAlias(scope.$index, scope.row)">名称</el-button>
                    <el-button size="mini" type="primary" @click="handleEditAmount(scope.$index, scope.row)">总量</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
              <div style="text-align: center;">
              <el-pagination background  layout="prev, pager, next" @current-change="handleCurrentChange" :total="50" :current-page.sync="currentpage">
              </el-pagination>
            </div>
            </basic-container>
          </div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import BasicContainer from "@vue-materials/basic-container";
import axios from "../../api/http.js";
const searchParams = {
  location_id: null,
  location_city: null,
  location_district: null,
  location_address: null,
  location_alias: null,
  url: "",
  pageNum: 1
};
export default {
  name: "LocationManage",
  components: {
    BasicContainer
  },
  data() {
    return {
      tableData: [
        // {
        //   admin_id: 1,
        //   admin_account: "admin"
        // }
        // {
        //   location_id: 1,
        //   location_city: "广州市",
        //   location_district: "番禺区",
        //   location_address: "广东药科大学图书馆xx咖啡厅",
        //   location_alias: "xx咖啡厅",
        //   location_amount: 20,
        //   location_available: 18
        // }
      ],
      searchForm: {
        locationId: null,
        locationCity: null,
        locationDistrict: null,
        locationAddress: null,
        locationAlias: null
      },
      currentpage: 1
    };
  },
  mounted: function() {
   (searchParams.locationId = null),
        (searchParams.locationCity = null),
        (searchParams.locationDistrict = null),
        (searchParams.locationAddress = null),
        (searchParams.locationAlias = null),
        (searchParams.url = "/manage/locations"),
        (searchParams.pageNum = 1);
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
  },
  methods: {
    handleEditAddress(index, row) {
      console.log(index, row);
      let locationId = row.location_id;
      let newAddress = null;
       this.$prompt("请输入新地址", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消"
          })
            .then(({ value }) => {
              newAddress = value;
              let updateParams = new URLSearchParams();

              updateParams.append("address", newAddress);
              if (locationId != "" && newAddress != "" ) {
                axios({
                  method: "POST",
                  url:
                    "/manage/locations/" + locationId,
                  params: updateParams
                }).then(res => {
                  if (res.data.code == 1) {
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      type: "warning",
                      message: res.data.msg
                    });
                  }
                });
              }
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "取消修改"
              });
            });
    },
    handleEditAlias(index, row) {
      console.log(index, row);
      let locationId = row.location_id;
      let alias = null;
       this.$prompt("请输入新名称", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消"
          })
            .then(({ value }) => {
              alias = value;
              let updateParams = new URLSearchParams();
              updateParams.append("alias", alias);
              if (locationId != "" && alias != "" ) {
                axios({
                  method: "POST",
                  url:
                    "/manage/locations/" + locationId,
                  params: updateParams
                }).then(res => {
                  if (res.data.code == 1) {
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      type: "warning",
                      message: res.data.msg
                    });
                  }
                });
              }
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "取消修改"
              });
            });
    },
    handleEditAmount(index, row) {
      console.log(index, row);
      let locationId = row.location_id;
      let amount = null;
       this.$prompt("请输入新容量", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消"
          })
            .then(({ value }) => {
              amount = value;
              let updateParams = new URLSearchParams();
              updateParams.append("amount", amount);
              if (locationId != "" && amount != "" ) {
                axios({
                  method: "POST",
                  url:
                    "/manage/locations/" + locationId,
                  params: updateParams
                }).then(res => {
                  if (res.data.code == 1) {
                    this.$message({
                      type: "success",
                      message: res.data.msg
                    });
                  } else {
                    this.$message({
                      type: "warning",
                      message: res.data.msg
                    });
                  }
                });
              }
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "取消修改"
              });
            });
    },
    handleDelete(index, row) {
      console.log(index, row);
      this.$confirm("此操作将永久删除该用户, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        center: true
      })
        .then(() => {
          axios({
            method: "DELETE",
            url: "/manage/administrators/" + row.admin_id
          }).then(res => {
            if (res.data.code == 1) {
              this.$message({
                type: "success",
                message: res.data.msg
              });
            } else {
              this.$message({
                type: "warning",
                message: res.data.msg
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    submitSearchFormById() {
      (searchParams.locationId = this.searchForm.location_id),
        (searchParams.locationCity = null),
        (searchParams.locationDistrict = null),
        (searchParams.locationAddress = null),
        (searchParams.locationAlias = null),
        (searchParams.url = "/manage/locations/" + searchParams.locationId),
        (searchParams.pageNum = 1);
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },
        submitSearchFormByAddress() {
      (searchParams.locationId = null),
        (searchParams.locationCity = this.searchForm.location_city),
        (searchParams.locationDistrict = this.searchForm.location_district),
        (searchParams.locationAddress = this.searchForm.location_address),
        (searchParams.locationAlias = null),
        (searchParams.url = "/manage/locations"),
        (searchParams.pageNum = 1);
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },
submitSearchFormByAlias() {
      (searchParams.locationId = null),
        (searchParams.locationCity = null),
        (searchParams.locationDistrict = null),
        (searchParams.locationAddress = null),
        (searchParams.locationAlias = this.searchForm.location_alias),
        (searchParams.url = "/manage/locations/alias/" + searchParams.locationAlias),
        (searchParams.pageNum = 1);
      this.currentpage = 1;
      this.getTable(searchParams.url, searchParams);
    },

    resetSearchForm(formName) {
      this.$refs[formName].resetFields();
    },

    getTable(targeturl, getparams) {
      console.log("url : " + targeturl + "   params : " + getparams);
      axios({
        method: "GET",
        url: targeturl,
        params: getparams
      }).then(res => {
        this.tableData = res.data.data;
        if (res.data.code < 1) {
          this.$message({
            type: "warning",
            message: res.data.msg
          });
        }
      });
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      searchParams.pageNum = val;
      this.getTable(searchParams.url, searchParams);
    }
  }
};
</script>
